/* стили основного контейнера слайдера */
.slider {
  position: relative;
  // overflow: hidden;
}

/* стили для обёртки, в которой заключены слайды */
.slider__wrapper {
  position: relative;
  overflow: hidden;
}

/* стили для контейнера слайдов */
.slider__items {
  display: flex;
  transition: transform 0.7s ease;
  list-style: none;
  margin: 0;
  padding: 0;
}



