body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Playfair Display", serif;
  font-size: 14px;
  line-height: 2;
  font-weight: normal;
  font-style: normal;
}
.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  margin: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
.article-list {
  width: 100%;
}
.article-list__item {
  flex: 0 0 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
}
/* стили для индикаторов */
.article-list__indicators {
  position: absolute;
  right: 0;
  bottom: 15px;
  left: 0;
  z-index: 15;
  display: flex;
  justify-content: center;
  padding-left: 0;
  margin-right: 15%;
  margin-left: 15%;
  list-style: none;
  margin-top: 0;
  margin-bottom: 0;
}
.article-list__indicators li {
  box-sizing: content-box;
  flex: 0 1 auto;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin-right: 7.5px;
  margin-left: 7.5px;
  text-indent: -999px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.5);
  background-clip: padding-box;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
}
.article-list__indicators li.active {
  background-color: #fff;
}
.article-list__control {
  display: none;
}
@media (min-width: 1000px) {
  .article-list {
    width: 100%;
  }
  .article-list__indicators {
    display: none;
  }
  .article-list__control {
    position: absolute;
    border: none;
    top: 260px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    color: #FFFFFF;
    text-align: center;
    opacity: 1;
    height: 50px;
    transform: translateY(-50%);
    background: transparent;
  }
  .article-list__control:hover,
  .article-list__control:focus {
    color: #fff;
    text-decoration: none;
    outline: 0;
    opacity: 0.9;
  }
  .article-list__control_prev {
    left: 50%;
    transform: translateX(-33vw);
  }
  .article-list__control_next {
    right: 50%;
    transform: translateX(33vw);
  }
  .article-list__control::before {
    display: none;
  }
  .article-list__control_prev::after {
    content: "";
    display: block;
    width: 64px;
    height: 1px;
    background: none;
    background-color: #FFFFFF;
    margin: 10px;
  }
  .article-list__control_next::before {
    content: "";
    display: block;
    width: 64px;
    height: 1px;
    background: none;
    background-color: #FFFFFF;
    margin: 10px;
  }
}
