  color: #FFFFFF;
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 43px;
  letter-spacing: 2px;
}
.link::after {
  content: "";
  background-image: url(../img/arrow-link.svg);
  background-repeat: no-repeat;
  width: 25px;
  height: 25px;
  display: inline-block;
  vertical-align: middle;
}
.link--light {
  background-color: #FF7931;
}
.link-event {
  margin: 0 auto;
  width: 288px;
}
.link-event__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.link-event__item {
  margin: 0;
  padding: 0;
  position: relative;
  margin-bottom: 30px;
}
.link-event__img {
  width: 100%;
  height: auto;
  display: block;
}
.link-event__title {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin: 0;
  top: 36px;
  font-family: Playfair Display, serif;
  font-style: normal;
  font-weight: 900;
  font-size: 20px;
  line-height: 180%;
  text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #FFFFFF;
}
.link-event__title--light {
  color: #000000;
}
.link-event__link {
  position: absolute;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);
}
@media (min-width: 1000px) {
  .link-event {
    width: 100%;
    margin: 0;
  }
  .link-event__list {
    display: grid;
    grid-gap: 30px;
  }
  .link-event__item {
    margin: 0;
  }
  .link-event__title {
    font-size: 30px;
  }
}
.ballet {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 50px;
  position: relative;
  align-self: end;
}
.ballet__img {
  width: 100%;
  display: block;
}
.ballet__text-wrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
  bottom: 42px;
  align-items: center;
}
.ballet__title {
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 2px;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 20px;
}
@media (min-width: 1000px) {
  .ballet {
    width: 100%;
    align-self: end;
    margin: 0;
  }
  .ballet__img {
    width: 100%;
    display: block;
  }
  .ballet__title {
    font-size: 30px;
    line-height: 35px;
  }
  .ballet__text-wrapper {
    flex-direction: row;
    width: 544px;
    justify-content: space-between;
    left: 50%;
    transform: translateX(-50%);
    bottom: 45px;
  }
}
